body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
}

.card__picture {
  justify-content: center;
  margin: 0 0 40px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__cost {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

